import React from "react"
import styled from "styled-components"
export const WrapperPolitica = styled.div`
  max-width: 65rem;
  margin: 0 auto;
  font-size: ${({ theme }) => theme.fontsize.medium};
`
export const Title = styled.h2`
  text-align: center;
  color: ${({ theme }) => theme.colors.azulOscuro};
  font-size: ${({ theme }) => theme.fontsize.extralarge};
  margin: 0;
  text-transform: uppercase;
`
export const Text = styled.p`
  color: ${({ theme }) => theme.colors.azulOscuro};
`
const Lista = styled.ul`
  margin-left: 2rem;
`
const Item = styled.li`
  color: ${({ theme }) => theme.colors.azulOscuro};
  margin-bottom: 0.7rem;
`
const Politica = () => {
  return (
    <div className="container wrapper">
      <WrapperPolitica>
        <Title>Politica Integrada</Title>
        <Text>
          En EPC INGENIERA, el enfoque de la Poltica del Sistema Integrado de
          Gestin es brindar a nuestros clientes servicios de diseo,
          ingeniera, fabricacin, soldadura especial, pintado, montaje de
          estructuras metalmecnicas, y recuperacin de componentes de equipos
          pesados, cumpliendo compromisos legales y requisitos en cuanto a
          costo, plazo, calidad, seguridad y salud en el trabajo y Medio
          Ambiente.
        </Text>
        <Text>Para lo cual nos comprometemos a:</Text>
        <Lista>
          <Item>	Cumplir con la normativa legal vigente y requisitos aplicables a la organizacin en materia de Calidad, Seguridad y Salud en el trabajo y Medio Ambiente.</Item>
          <Item>	Planificar y desarrollar todas las actividades proporcionando condiciones de trabajo seguras y saludables para la prevencin de lesiones y deterioros de la salud a fin de eliminar los peligros y reducir los riesgos significativos.</Item>
          <Item>	Garantizar la participacin, consulta de los trabajadores y sus representantes.</Item>
          <Item>	Proveer la capacitacin, sensibilizacin y competencia adecuada para todo el personal que labora en la organizacin, a fin de contar con personal competente, as como promover y motivar la conciencia en materia de Calidad, Seguridad y Salud en el trabajo y medio ambiente.</Item>
          <Item>	Garantizar la proteccin del medio ambiente, a travs de la prevencin de la contaminacin y reduciendo la generacin de residuos slidos.</Item>
          <Item>	Mejorar continuamente el desempeo de nuestro Sistema Integrado de Gestin. </Item>
        </Lista>
        {/* <Text>
        Edwin Palacios - Gerente General.
        </Text> */}
      </WrapperPolitica>
    </div>
  )
}

export default Politica
